.title {
  @apply mt-10 mb-4 text-center text-xl text-slate-900 font-bold;
}

.weekModule {
  @apply py-4 px-10 gap-6 max-w-[90vw] md:max-w-[60vw] overflow-x-scroll mx-auto flex mb-8 bg-white bg-opacity-40 shadow-xl rounded-md;
}

.weekModule::-webkit-scrollbar {
  width: 5px;
}

.weekModule::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: linear-gradient(45deg, rgb(15 23 42), rgb(38, 56, 90));
}

.weekModule::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  background-color: rgba(71, 85, 105, 0);
}

.item {
  @apply flex flex-col justify-center items-center text-center;
}